<template>
  <section>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-document"></i> </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>


    <div class="form-box">
      <el-form ref="userInfoForm" :model="userInfoForm" :rules="rules" label-width="80px">
        <el-form-item label="" prop="username">
          <el-col :span="12">
            <el-input v-model="userInfoForm.username" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-col :span="12">
            <el-input type="password" v-model="userInfoForm.password" placeholder=""></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-col :span="12">
            <el-input type="password" v-model="userInfoForm.confirmPassword" placeholder=""></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="sex">
          <el-col :span="12">
            <el-select v-model="userInfoForm.sex" placeholder="" style="width: 100%">
              <el-option key="" label="" :value="1"></el-option>
              <el-option key="" label="" :value="2"></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="age">
          <el-col :span="12">
            <el-input type="text" v-model="userInfoForm.age" placeholder=""></el-input>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="work">
          <el-col :span="12">
            <el-select v-model="userInfoForm.work" placeholder="" style="width: 100%">
              <el-option v-for="item in workOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="location">
          <el-col :span="12">
            <el-cascader :options="locationOptions" v-model="userInfoForm.location" @change="handleChange"
                         style="width: 100%" placeholder=""></el-cascader>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="salary">
          <el-col :span="12">
            <el-select v-model="userInfoForm.salary" placeholder="" style="width: 100%">
              <el-option key="1" label="1000" :value="1"></el-option>
              <el-option key="2" label="1000-1999" :value="2"></el-option>
              <el-option key="3" label="2000-3999" :value="3"></el-option>
              <el-option key="4" label="4000-5999" :value="4"></el-option>
              <el-option key="5" label="6000-9999" :value="5"></el-option>
              <el-option key="6" label="10000-19999" :value="6"></el-option>
              <el-option key="7" label="20000" :value="7"></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="marriage">
          <el-col :span="12">
            <el-select v-model="userInfoForm.marriage" placeholder="" style="width: 100%">
              <el-option key="0" label="" :value="0"></el-option>
              <el-option key="1" label="" :value="1"></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item>
          <el-col :span="12">
            <el-button type="primary" @click="onSubmit('userInfoForm')" style="width: 40%"></el-button>
            <el-button @click="onCancel" style="width: 40%"></el-button>
          </el-col>
        </el-form-item>
      </el-form>
    </div>

  </section>
</template>

<script>
  import {provinceAndCityData, CodeToText} from 'element-china-area-data'
  import {isRepeatedName,userRegister} from '../../api/api'

  const axios = require('axios');

  export default {
    name: "register",
    data() {

      let checkPassword = (rule, value, callback) => {
        if (value !== this.userInfoForm.password) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      let checkRepeatedUsername = (rule, value, callback) => {
        let para = {'username': value};
        isRepeatedName(para).then((res) => {
          if (res.data === false) {
            callback(new Error(''));
          } else {
            callback();
          }
        });
      };
      return {
        locationOptions: provinceAndCityData,

        workOptions: [{value: 'A', label: ''},
          {value: 'B', label: ''},
          {value: 'C', label: ''},
          {value: 'D', label: ''},
          {value: 'E', label: ''},
          {value: 'F', label: ''},
          {value: 'G', label: ''},
          {value: 'H', label: ''},
          {value: 'I', label: ''},
          {value: 'J', label: ''},
          {value: 'K', label: ''},
          {value: 'L', label: ''},
          {value: 'M', label: ''},
          {value: 'N', label: ''},
          {value: 'O', label: ''},
          {value: 'P', label: ''},
          {value: 'Q', label: ''},
          {value: 'R', label: ''},
          {value: 'S', label: ''},
          {value: 'T', label: ''},
          {value: 'Y', label: ''},
          {value: 'Z', label: ''}],
        // 
        userInfoForm: {
          username: '',
          password: '',
          age: '',
          sex: [],
          work: [],
          location: [],
          salary: [],
          marriage: []
        },
        // 
        rules: {
          username: [{
            required: true, message: '', trigger: 'blur'
          }, {
            validator: checkRepeatedUsername, trigger: 'blur'
          }],
          password: [
            {required: true, message: '', trigger: 'blur'}
          ],
          confirmPassword: [
            {validator: checkPassword, trigger: 'blur'}
          ],
          age: [{
            required: true, message: '', trigger: 'blur'
          }],
          sex: [{
            required: true, message: '', trigger: 'blur'
          }],
          work: [{
            required: true, message: '', trigger: 'blur'
          }],
          location: [{
            required: true, message: '', trigger: 'blur'
          }],
          salary: [{
            required: true, message: '', trigger: 'blur'
          }],
          marriage: [{
            required: true, message: '', trigger: 'blur'
          }],

        },
      }

    },
    methods: {
      handleChange(value) {
        // change 
        this.userInfoForm.location = value;
      },
      onSubmit(formName) {
        const self = this;
        self.$refs[formName].validate((valid) => {
          if (valid) {
            this.$message({message: '', type: 'success'});
            // 
            let userInfoData = {
              username: this.userInfoForm.username,
              password: this.userInfoForm.password,
              age: this.userInfoForm.age,
              sex: this.userInfoForm.sex,
              work: this.userInfoForm.work,
              location: CodeToText[this.userInfoForm.location[0]]+CodeToText[this.userInfoForm.location[1]],
              salary: this.userInfoForm.salary,
              marriage: this.userInfoForm.marriage
            };
            console.log(userInfoData);

            userRegister(userInfoData).then((res)=>{
              if(res.data===1){
                this.$message({
                  message:'',
                  type:'success'
                })
              }else{
                this.$message.error('');
              }
            });

            return true;
          } else {
            this.$message.error('');
            return false;
          }
        });
      },
      onCancel() {
        localStorage.removeItem('user');
        this.$router.replace('/login');
      },


    },
  }
</script>

<style scoped>

</style>



